(function () {
  'use strict';
  angular
    .module('angularTest')
    .controller('UploadVideoController',UploadVideoController);

  function UploadVideoController($http, $scope, toastr) {
    var vm = this;
    vm.progressBarActive = false;
    vm.videoList = [];

    var result;

    function init() {
      vm.progressBarActive = false;
      console.log("upload page started");
      getAllVideos();
    }

    function convertToMb(size) {
      if(size) {
        var mb = (size / 1024) / 1024;
        return mb;
      } else {
        return 0;
      }
    }

    function getAllVideos() {
      $http({
        url: 'https://api.wistia.com/v1/medias.json?api_password=6f2bf05ae1fcde069f539257ff7696ea3a6cd94be21dba0e5ec8d8386516d2cc',
        method: 'GET'
      }).success(function (response) {
        vm.videoList = [];
        if(response.length > 0) {
          for(var i = 0; i < response.length; i++) {
            var row = response[i];
            var newSize = convertToMb(row.assets[0].fileSize).toFixed(2);
            vm.videoList.push({
              id: row.id,
              image: row.thumbnail.url,
              url: row.assets[0].url,
              title: row.name,
              size: newSize + ' MB'
            });
          }
        }
      }).error(function (response) {
        toastr.error("Error: " + response);
      });
    }

    $(function() {
      $('#submit').fileupload({
        url:'https://upload.wistia.com/?api_password=6f2bf05ae1fcde069f539257ff7696ea3a6cd94be21dba0e5ec8d8386516d2cc',
        dataType: 'json',
        sequentialUploads: true,
        progressall: function (e, data) {
          if(!vm.progressBarActive) {
            vm.progressBarActive = true;
            $scope.$apply();
          }
          var progress = parseInt(data.loaded / data.total * 100, 10);
          $('#progress .progress-bar').css(
            'width',
            progress + '%'
          );
        },
        done: function (e, data) {
          if(vm.progressBarActive) {
            vm.progressBarActive = false;
            $scope.$apply();
            $('#progress .progress-bar').css('width', '0%');
          }

          if(data.response()){
            result = data.response().result;
          }

          getAllVideos();
          /*vm.videoList.push({
            id: result.id,
            image: result.thumbnail.url,
            title: result.name,
            size: convertToMb(data.total).toFixed(2) + ' MB'
          });*/

          toastr.success(data.response().textStatus);
          
        },
        error: function (jqXHR, textStatus, errorThrown) {
          if(jqXHR.responseJSON) {
            toastr.error(jqXHR.responseJSON.error);
          }
          if(vm.progressBarActive) {
            vm.progressBarActive = false;
            $scope.$apply();
            $('#progress .progress-bar').css('width', '0%');
          }
        }
      });
    });

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('angularTest')
    .directive('navbarHeader', navbarHeader);

  /** @ngInject */
  function navbarHeader() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment, $state) {
      var vm = this;
      vm.tabActive = true;
      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();

      vm.goToUpload = function () {
        vm.tabActive = false;
        $state.go('about');
      };

      vm.homeActive = function () {
        vm.tabActive = true;
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('angularTest')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('angularTest')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

